<template>
   
  <div class="container">
       <b-navbar>
        <template #brand>
            <b-navbar-item tag="a" href="https://khoitaodoanhnghiep.com/">
                <img
                    src="https://khoitaodoanhnghiep.vn/wp-content/uploads/2019/06/logo-1.png"
                    alt="FADI JSC"
                >
            </b-navbar-item>
        </template>
        <template #end>
            <b-navbar-item href="https://khoitaodoanhnghiep.com/">
               Trang chủ
            </b-navbar-item>
            <b-navbar-dropdown label="Bảng giá">
                <b-navbar-item href="https://khoitaodoanhnghiep.com/bang-gia-thanh-lap-cong-ty/">
                   Bảng giá thành lập công ty
                </b-navbar-item>
                <b-navbar-item href="https://khoitaodoanhnghiep.com/bang-gia-thay-doi-noi-dung-cong-ty/">
                    Bảng giá thay đổi nội dung
                </b-navbar-item>
            </b-navbar-dropdown>
        </template>
    </b-navbar>
    <h1 class="primary my-5 has-text-centered is-size-2	"> DANH SÁCH KHÁCH HÀNG CỦA FADI </h1>
    <section>
      <b-table
        :data="isEmpty ? [] : data"
        :bordered="true"
        :striped="true"
        :narrowed="true"
        :hoverable="true"
        :columns="columns"
        :paginated="true"
        :per-page="15"
        :current-page.sync="currentPage"
        :rounded="true"
        :pagination-rounded="true"
        pagination-size="is-small"
        pagination-order="is-centered"
      >
        <template v-for="(column, index) in columns">
          <b-table-column :key="index" v-bind="column">
            <template
              v-if="column.searchable && !column.numeric"
              #searchable="props"
            >
              <b-input size="is-small" v-model="props.filters[props.column.field]" />
            </template>
            <template v-slot="props">
              {{  props.row[column.field] }}
            </template>
          </b-table-column>
        </template>
      </b-table>
    </section>
  </div>
</template>

 <script>
import { Sheet1 } from "./data/companies-data.json";

export default {
  name: "App",
  data() {
    const data = Sheet1.map((el) => this.capitalizeObj(el));
    // console.log(data)
    const columns = [
      {
        field: "GIÁM ĐỐC",
        label: "Tên chủ sở hữu",
        centered: true,
        searchable: true,
      },
      {
        field: "TÊN CÔNG TY",
        label: "Tên công ty",
        searchable: true,
      },
      {
        field: "ĐỊA CHỈ",
        label: "Địa chỉ",
        centered: true,
        searchable: true,
      },
    ];
    return {
      data,
      columns,
      isEmpty: data.length === 0,
      currentPage: 1,
    };
  },
  methods: {
    capitalizeFirstLetter(string) {
        string = string.toLowerCase().trim();
        let conStr = string.split(' ').map(str => str.trim().charAt(0).toUpperCase() + str.slice(1))
        return  conStr.join(' ');
    },
    capitalizeObj(obj) {
      return {
        "TÊN CÔNG TY": obj["TÊN CÔNG TY"]
          ? this.capitalizeFirstLetter(obj["TÊN CÔNG TY"])
          : NaN,
        "ĐỊA CHỈ":  obj["ĐỊA CHỈ"]
          ? this.capitalizeFirstLetter(obj["ĐỊA CHỈ"])
          : NaN,
        "GIÁM ĐỐC": obj["GIÁM ĐỐC"]
          ? this.capitalizeFirstLetter(obj["GIÁM ĐỐC"])
          : NaN,
      };
    },
  },
};
</script>

<style>
.th-wrap {
    display: flex;
    justify-content: center;
}
.control{
    width: 100%;
}
</style>